<p><br></p>
<p><span class="rvts17">施法类</span></p>
<p><span class="rvts33">1. </span><span class="rvts29">祝福（</span><span class="rvts33">Bless</span><span class="rvts29">）</span>
<br><span class="rvts21">玩家在清理过川普多洞穴（</span><span class="rvts20">Trompdoy's Cavern</span><span class="rvts21">），第一次拜访回音大厅后，便会学到该技能。该技能与诅咒（</span><span class="rvts20">Curse</span><span class="rvts21">）技能对立，不能同时拥有。</span></p>
