<p><span class="rvts26">英文</span></p>
</td>
<td width="78" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">记忆槽</span></p>
</td>
<td width="93" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts27">AP</span><span class="rvts26">消耗</span>
