<br><span class="rvts20">1</span></p>
</td>
<td width="52" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">3</span></p>
</td>
<td width="523" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">为目标或地面环境施加祝福效果，持续</span><span class="rvts20">2</span><span class="rvts21">轮，</span>
<br><span class="rvts21">祝福效果提供</span><span class="rvts20">15%</span><span class="rvts21">所有元素抵抗，</span>
<br><span class="rvts21">并且提高</span><span class="rvts20">20%</span><span class="rvts21">精准和</span><span class="rvts20">10%</span><span class="rvts21">躲闪</span></p>
</td>
</tr>
</table>
