<p><span class="rvts48"></span>
<br><span class="rvts33">2. </span><span class="rvts29">诅咒（</span><span class="rvts33">Curse</span><span class="rvts29">）</span>
<br><span class="rvts21">该技能的获取方式需要玩家队伍中有一名不死族队友，与无名岛中的一个虚空者交谈，并宣誓效忠神主（</span><span class="rvts20">God King</span><span class="rvts21">）。在获得该技能后，祝福（</span><span class="rvts20">Bless</span><span class="rvts21">）技能将被取代。</span></p>
