<p><span class="rvts48"></span>
<br><span class="rvts33">3. </span><span class="rvts29">源力吸取（</span><span class="rvts33">Source Vampirism</span><span class="rvts29">）</span>
<br><span class="rvts21">该技能在任务第二章的</span><span class="rvts20"></span><span class="rvts21">强力觉醒（</span><span class="rvts20">Powerful Awakening</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">任务中可获得，解锁第二个源力值，并返回回音大厅。获得该技能后，玩家将在之后的对话中，可以使用该技能直接杀掉灵魂类生物。</span></p>
<p><span class="rvts21">然而，尽可能的小心和这些鬼魂谈话。另外，该技能无法从烧焦和被电焦的尸体中获得源力值。</span></p>
